import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { Button, Row, Image, Modal, Col, Container, Form, InputGroup, FormControl } from 'react-bootstrap';
import { Cart4 } from 'react-bootstrap-icons';
import useModal from './ModalCard';
import useWebState from './WebState';

const ButtonAddShoppingCartGoods = ({ onConfirm, value }) => {
    // 參數
    const url = 'http://localhost:8088';
    const { showModal, handleShowModal, handleCloseModal } = useModal();
    const { webState } = useWebState();
    const [result, setResult] = useState(false);
    // 加入購物車
    const handleSubmitAddShoppingCartGoods = async (event) => {
        event.preventDefault();
        onConfirm(event); // 回傳下方按鈕 handleSaveClick 結果 確認:true \ 取消:false
        if (event.target.goodsQuantity.value > 0 && event.target.goodsQuantity.value !== '') {
            if (onConfirm) {
                const objform = {
                    buyQuantity: event.target.goodsQuantity.value,
                    goodsID: value
                };
                await addShoppingCartGoods(objform, (success) => {
                    success && (event.target.goodsQuantity.value = '');
                    setResult(success);
                });
            }
        } else {
            setResult(false);
        }
        handleShowModal(event);
    };

    // 加入購物車
    const addShoppingCartGoods = async (objform, callback) => {
        const apiUrl = url + '/training/FrontendController/addCartGoods';
        const design = '/ReactDemo/Data/Frontend/Product/ProductAddShoppingCartGoods.json';
        const response = await axios.get((webState ? design : apiUrl), objform).then((rs) => rs.data).catch((error) => { console.log(error); });
        callback(response !== undefined);

    };

    useEffect(() => {

    }, [result])

    return (
        <div>
            <>
                <Form onSubmit={handleSubmitAddShoppingCartGoods}>
                    <InputGroup className="mb-1">
                        <FormControl type="number" name="goodsQuantity" autoComplete="off" />
                        <Button className="btn btn-primary btn-sm" type="submit"><Cart4 /></Button>
                    </InputGroup>
                </Form>
            </>
            <>
                <Modal show={showModal} onHide={handleCloseModal} animation={false} centered>
                    <Modal.Header closeButton>
                        <Modal.Title>信息提示:</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Container>
                            {(result) ? (
                                <>
                                    <Row>
                                        <Col xs={12} md={12} className='text-center'>
                                            <Image style={{ width: '200px', height: '200px' }} src="/ReactDemo/assets/imgs/addSuccess.png" rounded />
                                        </Col>
                                    </Row>
                                    <br />
                                    <Row>
                                        <Col xs={12} md={12} className='text-center'>
                                            加入購物車成功!
                                        </Col>
                                    </Row>
                                </>
                            ) : (
                                <>
                                    <Row>
                                        <Col xs={12} md={12} className='text-center'>
                                            <Image style={{ width: '200px', height: '200px' }} src="/ReactDemo/assets/imgs/AddZero.jpg" rounded />
                                        </Col>
                                    </Row>
                                    <br />
                                    <Row>
                                        <Col xs={12} md={12} className='text-center'>
                                            加入商品數量有誤 !
                                        </Col>
                                    </Row>
                                </>
                            )}
                        </Container>
                        <br />
                        <br />
                        <br />
                    </Modal.Body>
                </Modal>
            </>
        </div>
    )
}

export default ButtonAddShoppingCartGoods
